import React from 'react';
import './App.css';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import Home from './pages/';
import SigninPage from './pages/signin';

function App() {
  return (
    <div>
      <Router>
        <Switch>
          <Route path="/React-Project-1/" component={Home} exact />
          <Route path="/React-Project-1/signin" component={SigninPage} exact />
        </Switch>
      </Router>
    </div>
  );
}

export default App;
