import React from 'react';
import {animateScroll as scroll} from 'react-scroll';
import {FaFacebook, FaInstagram, FaTwitter, FaYoutube, FaLinkedin} from 'react-icons/fa'
import {FooterContainer, FooterWrap, FooterLinksContainer, FooterLinksWrapper, FooterLinkItems, FooterLinkTitle, FooterLink, SocialMedia, SocialMediaWrap, SocialLogo, WebsiteRights, SocialIcons, SocialIconLink} from './FooterElements';

const Footer = () => {
    const toggleHome = () => {
        scroll.scrollToTop();
    };

    return (
        <FooterContainer>
            <FooterWrap>
                <FooterLinksContainer>
                    <FooterLinksWrapper>
                        <FooterLinkItems>
                            <FooterLinkTitle>
                                About us
                            </FooterLinkTitle>
                                <FooterLink to="/React-Project-1/signin">How it works</FooterLink>
                                <FooterLink to="/React-Project-1/signin">Testimonials</FooterLink>
                                <FooterLink to="/React-Project-1/signin">Investors</FooterLink>
                                <FooterLink to="/React-Project-1/signin">Terms of Service</FooterLink>
                        </FooterLinkItems>
                        <FooterLinkItems>
                            <FooterLinkTitle>
                                Videos
                            </FooterLinkTitle>
                                <FooterLink to="/React-Project-1/signin">Submit Video</FooterLink>
                                <FooterLink to="/React-Project-1/signin">Ambassadors</FooterLink>
                                <FooterLink to="/React-Project-1/signin">Agency</FooterLink>
                                <FooterLink to="/React-Project-1/signin">Influencer</FooterLink>
                        </FooterLinkItems>
                    </FooterLinksWrapper>
                    <FooterLinksWrapper>
                        <FooterLinkItems>
                            <FooterLinkTitle>
                                Contact Us
                            </FooterLinkTitle>
                                <FooterLink to="/React-Project-1/signin">Contact</FooterLink>
                                <FooterLink to="/React-Project-1/signin">Support</FooterLink>
                                <FooterLink to="/React-Project-1/signin">Destinations</FooterLink>
                                <FooterLink to="/React-Project-1/signin">Sponsorships</FooterLink>
                        </FooterLinkItems>
                        <FooterLinkItems>
                            <FooterLinkTitle>
                                Social Media
                            </FooterLinkTitle>
                                <FooterLink to="/React-Project-1/signin">Instagram</FooterLink>
                                <FooterLink to="/React-Project-1/signin">Facebook</FooterLink>
                                <FooterLink to="/React-Project-1/signin">Youtube</FooterLink>
                                <FooterLink to="/React-Project-1/signin">Twitter</FooterLink>
                        </FooterLinkItems>
                    </FooterLinksWrapper>
                </FooterLinksContainer>
                <SocialMedia>
                    <SocialMediaWrap>
                        <SocialLogo to='/React-Project-1' onClick={toggleHome}>
                            Hello
                        </SocialLogo>
                        <WebsiteRights>
                            Hello &copy; {new Date().getFullYear()} All rights reserved.
                        </WebsiteRights>
                        <SocialIcons>
                            <SocialIconLink href='/' target="_blank" aria-label="Facebook">
                                <FaFacebook />
                            </SocialIconLink>
                            <SocialIconLink href='/' target="_blank" aria-label="Instagram">
                                <FaInstagram />
                            </SocialIconLink>
                            <SocialIconLink href='/' target="_blank" aria-label="Youtube">
                                <FaYoutube />
                            </SocialIconLink>
                            <SocialIconLink href='/' target="_blank" aria-label="Twitter">
                                <FaTwitter />
                            </SocialIconLink>
                            <SocialIconLink href='/' target="_blank" aria-label="Linkedin">
                                <FaLinkedin />
                            </SocialIconLink>
                        </SocialIcons>
                    </SocialMediaWrap>
                </SocialMedia>
            </FooterWrap>
        </FooterContainer>
    )
}

export default Footer
